.project-group {
    height:60rem;
    width:100%;
    position:relative;
    z-index:1;
}
.project {
    display:flex;
    justify-content:center;
    flex-wrap:wrap;
    align-items:flex-start;
    margin: 1rem 0 5rem 0;
    position:absolute;
    width:100%;
    height:100%;
    opacity:0;
    transition:all ease-in-out .8s;
    transform:translateX(-60%);

    &--shown {
        opacity:1;
        z-index:2;
        transform:translateX(0);
    }

    &__title {
        display:block;
        font-weight:400;
        font-size:3rem;
    }

    &__text {
        flex:1;
    }

    &__subtitle {
        display:block;
        font-size:2.2rem;
    }

    &__preview {
        margin:3rem;
        flex: 1;
        display:flex;
        flex-direction:column;
    }
    &__img {
        border-radius:.8rem;
        box-shadow: .3rem .3rem .5rem 0px $black;
        width:100%;
        transition: all .3s;
        &:hover {
            transform: scale(1.05);
            //box-shadow: 0 0 
        }
        
    }
    &__buttons {
        display:flex;
        height:5rem;
        flex-direction: row;
        justify-content:center;
        align-items:center;
        margin-top:2rem;
    }

}